//! button
.com_btn {
	position: relative;
	padding: 0 20px;
	border-radius: 6px;
	min-width: 85px;
	@include inflex(all);
	letter-spacing: normal;
	box-sizing: border-box;
	letter-spacing: -0.025em;
	color: #fff;
  gap:8px;
  &:visited,
  &:link,
  &:active,
  &:hover{
    color: #fff;
  }
	&_wrap {
		@include flex(x);
		gap:10px;
    position:relative;
		&.between {
			@include flex(between);
		}
		&.flex {
			flex-wrap: wrap;
		}
    &.bottom{
      margin-top:30px;
			@include media-min($m) {
					gap:20px;
					.com_btn{
						min-width: 200px;
					}
			}
			@include media-max($l) {
					gap:10px;
			}
			@include media-max($m) {
				.com_btn{
					flex:1;
				}
			}
    }
    &.has_paging{
      margin-top:70px;
      @include media-max($l){
        margin-top:20px;
      }
      @include media-max($m){
        @include flex(column);
        gap:10px;
        .com_paging{
          order:2;
        }
      }
      .right{
        @include flex(y);
        gap:10px;
        @include media-min($m) {
          position: absolute;
          right:0;
          top:0;
          .com_btn{
            min-width: 0;
          }
        }
        @include media-max($l){
          top:0;
        }
        @include media-max($m){          
          .com_btn{flex:1;}
          order:1;
        }
      }
    }
		&.has_btn_right {
			@include media-max($l) {
			flex-direction: column;
			}
			.btn_right {
				@include media-min($l) {
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				}
				display: inline-flex;
				align-items: center;
				gap:5px;
				font-size: 16px;
				color:#222;
				@include media-max($l) {
						font-size: 13px;
						order:-1;
						margin-left: auto;
						margin-bottom: 10px;
				}
				.dark & {
				color:#fff;
				
				}
			}
			.com_btn.l{
				line-height: 60px;
			}
		}
		&.right{
			justify-content: flex-end;
		}
    .com_paging{
      margin-top:5px;
    }
	}
	&.full {
		width: 100%;
		flex: 1;
	}
	&.m_full{
		@include media-max($m) {
			width: 100%;
			flex: 1;
		}
	}
	&.l_full{
		@include media-max($l) {
			width: 100%;
			flex: 1;
		}
	}
	//&컬러 color
	&.point {
		background-color: $point;
	}
	&.black {
		background-color: #000;
	}
	&.orange {
		background-color: $orange;
	}
	&.gray {
		background-color: $gray;
		color: #666;
		.dark & {
			background-color: #5b637a;
			color: #fff;
		}
	}
  &.dark_gray{
    background-color: #5B637A;
		color: #fff;
  }
	&.active {
		background-color: $gray;
		color: #fff;
	}
	&:disabled,
	&.disabled {
		background-color: #f1f0f5;
		color: #b1b3b7 !important;
	}
	//&유형 type
	&.line {
		border: solid 2px;
		background-color: $point;
		&.point {
			border-color: $point;
			background-color: $bg-gray;
			color: $point;
			.dark & {
				background-color: $gray-dark;
				//color:#fff;
			}
		}
		&.black {
			border-color: #5b637a;
			background-color: $bg-gray;
			color: $gray-dark;
			.dark & {
				background-color: $gray-dark;
				color: #d4d4d7;
			}
		}
		&.dark{
			background-color: #fff;
			border:1px solid #222;
			color:#222;
			.dark & {
				background-color: $bg-dark;
				color:#fff;
				border-color: #fff;
		 }
		}
		&.gray{
			border-color: $c-border;
			background-color:transparent;
			color:#222;
			.dark & {
				color: #fff;
				border-color: $c-border-dark-light;
			}

		}
    &.xs{border-width: 1px;}
	}
	&.round {
		border-radius: 100%;
	}
	&.circle {
		border-radius: 100%;
		aspect-ratio: 1 / 1;
		padding: 0;
	}
	&.txt {
		padding: 0 !important;
		background: none;
		color: $point;
		min-width: auto;
		text-decoration: underline;
		text-underline-offset: 5px;
		&.gray {
			color: #919191;
		}
    &.black{
      color:$fc-default;
      .dark &{color:#fff}
    }
	}
	//&사이즈 size
	&.l {
		height: 60px;
		font-weight: 700;
		font-size: 16px;
		&.oval {
			border-radius: 60px;
		}
	}
	&.m {
		height: 50px;
		font-weight: 600;
		//font-size: 12px; 14px 로 디자인 통일 11/11
		&.oval {
			border-radius: 50px;
		}
	}
	&.s {
		height: 36px;
		font-size: 12px;
		font-weight: 500;
		min-width: auto;
		&.oval {
			border-radius: 36px;
		}
	}
	&.xs {
		height: 30px;
		font-size: 12px;
		font-weight: 500;
    padding-inline: 13px;
		min-width: auto;
		&.oval {
			border-radius: 30px;
		}
	}
	&.xxs{
		min-width: auto;
		border-width: 1px;
		padding:2px 5px;
		font-size: 12px;
	}
	&.full {
		width: 100%;
	}
  &.extend{
    flex:1;
  }
	&.px_0{
		padding-inline: 0;
	}
	//&아이콘
	.svg_icon {
		&.icon_search{
      width:16px;
      min-width: 16px;
      height: 16px;
    }
	}
}

//!아이콘버튼
.com_btn_edit{
	width: 26px;
	height: 26px;
	border-radius: 50%;
	background-color: #fff;
	@include flex(all);
}
.com_btn_mike{
	@include flex(all);
	width: 46px;
	height: 46px;
	border-radius: 50%;
	box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.16);
	background-color: $point;
}

/*!페이징 << < > >> */
.com_paging {
  margin-top: 70px;
  text-align: center;
  @include media-max($l){
    margin-top:50px;
  }
  @include media-max($m){
    margin-top:30px;
  }
  :is(.btn_first, .btn_prev, .btn_page, .btn_next, .btn_last) {
    min-width: 27px;
    height: 27px;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    margin-left: 0;    
    border-radius: 5px;
  }
  :is(.btn_first, .btn_prev, .btn_next, .btn_last){    
    border: 1px solid #D4D4D7;
    margin-inline: 5px;
    @include media-max($l){
      margin-inline: 2px;
    }
    .dark &{
      border-color:#707070
    }
    img {
      height: 16px;
    }
  }
  :is(.btn_first, .btn_prev) {
    transform: rotate(180deg);
  }
  .btn_prev {
    margin-right: 50px;
    @include media-max($l){
      margin-right: 15px;
    }
  }
  .btn_next {
    margin-left: 50px;
    @include media-max($l){
      margin-left: 15px;
    }
  }
  .btn_page {
    line-height: inherit;
    color:#707070;
    margin-inline: 10px;
    @include media-max($l){
      margin-inline: 0px;
    }
    &.active {
      font-weight: bold;
      color: #222;
      border:1px solid #666;
      .dark &{
        color:#fff;
        border-color:#fff;
      }
    }
    &.disabled {
      cursor: auto;
      opacity: 0.4
    }
  }
}

