//!Checkbox 커스텀
//&일반 checkbox , radio
//  <label class="com_chk"><input type="checkbox">윙</label> //마크업은 수정 불가
.com_chk {
	@include inflex(y);
	gap: 7px;
	cursor: pointer;
	line-height: initial;
	.dark & {
		color: #fff;
	}
	&__wrap {
		@include flex(y);
		gap: 10px 20px;
		@include flex-wrap;
		//&.col4 {
		//	@include list(5px, 4, 13px);
		//}
	}
	input[type="text"],
	input[type="number"] {
		width: 80px;
		text-align: center;
	}
	input[type="checkbox"],
	input[type="radio"] {
		display: inline-block;
		padding-right: 0;
		height: 24px;
		&::before {
			@include beforeafter;
			width: 24px;
			height: 24px;
			background: #fff;
			border: 2px solid #bdbfc2;
			border-radius: 2px;
			background-image: url("../../img/svg/icon_chk.svg");
			background-position: center;
			background-size: 12px auto;
			background-repeat: no-repeat;
			cursor: pointer;
			.dark & {
				background-color: transparent;
				border-color: #444a56;
				background-image: url("../../img/svg/icon_chk_dk.svg");
			}
		}
		&:checked::before {
			background-image: url("../../img/svg/icon_chk_on.svg");
			border-color: $point;
		}
		&:disabled {
			opacity: 0.4;
			border:none;
		}
	}
	&.circle {
		position: relative;
		margin: 0;
		input[type="checkbox"],
		input[type="radio"] {
			&::before {
				border-radius: 50%;
				background-image: none;
			}
			&:checked::before {
				background-image: url("../../img/svg/icon_radio_on.svg");
				background-size: 24px auto;
			}
		}
	}
}
.com_chk_btn {
  cursor: pointer;
	&__wrap {
		@include flex(y);
		gap: 5px;
		@include flex-wrap;
	}
  input{
    &+.btn{
      height:40px;
      padding-inline:15px;
      @include inflex(all);
      font-weight:500;      
      font-weight: 600;
      white-space:nowrap;
      border:1px solid #222;
      border-radius:6px;
      cursor:pointer;
      width: 100%;
      line-height: initial;
      @include media-max($l){
        font-size:14px;
      }
      .dark &{
        color:#fff;
        border-color: #fff;
      }
    }
  }	
	input:checked{
	  &+.btn{
      color:#fff;
	    background-color:$point;
      border-color:$point;
	  }
	}
  input:disabled{   
		border:none; 
    &+.btn{
      background-color: #E2E7ED !important;
      color:#B1B3B7 !important;
      border:none !important;
      cursor: default;
      .dark &{
        background-color: $gray-dark !important;
        color:#5B637A !important;
      }
    }
  }
	&.gray{
		input{
			& +.btn{
				background-color: $gray;
				color:#B1B3B7;
				border-color:$gray;
			}
      .dark &:not(:checked)+.btn{
        background-color: #5B637A;
        border-color:#5B637A;
        color: #fff !important;
      }
		}
		input:checked{
			&+.btn{
				color:#fff;
				background-color:$point;
				border-color:$point;
			}
		}
	}
  &.line{
		input{
			& +.btn{
				background-color: transparent;
				color:$point;
				border-color:$point;
        .dark &{
          border-color:$c-border-dark-light;
          color:#fff;
        }
			}
		}
		input:checked{
			&+.btn{
				color:#fff;
				background-color:$point;
				border-color:$point;
			}
		}
	}
	&.l{
		input + .btn{
			height: 50px;
			border-radius: 6px;
			font-weight: 400;
		}
	}
}
.com_board_sort{
  @include flex(s);
  //@include flex-wrap;
  gap:10px;  
  @include media-min($m){
    @include flex-wrap;
  }
	@include media-max($l) {
		justify-content: center;
	}
  @include media-max($m){
		justify-content: flex-start;
    gap:5px;
    overflow-x: auto;
    width:100%;
		white-space: nowrap;
		background-color: #fff;
    .dark &{background-color: $bg-dark;}
		&::-webkit-scrollbar {
			display: none;
		}
  }
		&__wrap{
		position: relative;
		width: 100%;
