			.com_chk_btn {
				flex: 1;
				.btn {
					width: 100%;
				}
			}
		}
	}
  .com_chk_btn{
    .btn{
      font-size: 16px;
      padding-inline: 20px;
      border-radius:10px;
      @include media-min($l){
