.com_star{
  //별점 선택하기
  position:relative;
  direction: rtl;
  text-align: center;  
  input[type=radio]{
    display:none;
		 + .icon_star{
			@include svg-icon-color("icon_star","BDBFC2");
		}
    &:checked{
      & ~ .icon_star{
        @include svg-icon-color("icon_star","FD4957");
      }
    }
    &:disabled{
      &+.icon_star{
        cursor: default;
        width:18px;
        height:18px;
        margin-right:2px;
      }
    }
  }
  .icon_star{
    @include svg-icon-color("icon_star","BDBFC2");
    display:inline-block; 
    cursor:pointer; 
    margin-right:4px; 
    margin-left:0 !important;
    &:first-of-type{margin-right:0;}
    @include media-max($s) {
      margin-right: 0;
    }
  }
  //별점 보여주기
  &_display{
    width:105px !important;
    min-width:105px !important;
    @include media-max($l){
      width:95px !important;
      min-width:95px !important;
    }
    &.svg_icon.icon_star, .svg_icon.icon_star{
      background-repeat: repeat-x !important;
      background-position: left !important;
      @include media-max($l){
        height: 18px;
      }
    }
  }
}
