  }
}
.com_btn_subscribe{
  &:hover{
    cursor: pointer;
  }  
  .btn{
    height: 30px;
    padding-inline: 11px;
    @include flex(y);
    gap:5px;
    border-radius:30px;
    background-color: $point;
    color:#fff;
    font-weight: 600;
    .off{
      display: none;
    }
  }
  input{
    display: none;
    &:checked{
      &+.btn{
        .off{
          display: block;
        }
      }
    }
  }
}
.com_thumbup{
  @include flex(_);
  gap:5px;
  line-height: initial;
  font-weight: 700;
  cursor: pointer;
  input{
    &+.svg_icon{
      @include svg-icon-color("icon_thumbup","92949A");
    }
    &:checked{
      &+.svg_icon{
        @include svg-icon-color("icon_thumbup","49BCBD");
      }
    }
  }
}

