//!토글 스위치 버튼
.com_switch {
  &__wrap{
    position: relative;
		width: 40px;
  }
   width: 40px;
   height: 20px;
  display: inline-block;
  -webkit-appearance: none;
  background-color: #D9D9D9;
  border-radius: 100px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: all .3s ease;
  cursor: pointer;
  &:after {
    content: '';
    display: inline-block;
     width: 16px;
     height: 16px;
    background-color: #fff;
    border-radius: 50px;
    position: absolute;
    top: 50%;
    left: 2px;
    transform: translateY(-50%);
    transition: all .3s ease;
  }
  &:checked[type="checkbox"] {
     background-color: $point;
    transition: all .3s ease;
  }
  &:checked[type="checkbox"]:after {
    left: 22px;
    transition: all .3s ease;
    background-color: #fff;
    border:none;
  }
}
